export default {
    props: {
        labelCancel: {
            type: String,
            default: "取消"
        },
        labelConfirm: {
            type: String,
            default: "确定"
        },
        title: String
    },
    methods: {
        cancel: function() {
            this.$emit("cancel");
        },
        save: function() {
            this.$emit("save");
        },
        setTitle(event) {
            let { value } = event.detail;
            this.$emit("setTitle", value.trim());
        }
    }
};
